import React from 'react';
import Grid from '@material-ui/core/Grid';
import SingleProject from './single-project.component';
import projects from '../projects';
import IosArrowForward from 'react-ionicons/lib/IosArrowForward';
import IosArrowBack from 'react-ionicons/lib/IosArrowBack';



class Projects extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      currentProjectIndex: 2,
    }
    
    this.slideFoward = this.slideFoward.bind(this);
    this.slideBack = this.slideBack.bind(this);
  }


  projectItems(){
    let projectView = projects.slice(this.state.currentProjectIndex - 2, this.state.currentProjectIndex + 1);
    return projectView.map(project => {
      return (
          <SingleProject project={project} toggleModal={this.props.toggleModal}/>
      ) 
    })
  }

  slideFoward(){
    if(this.state.currentProjectIndex < 3){
      this.setState(({ currentProjectIndex }) => ({ currentProjectIndex: currentProjectIndex + 1 }), 
      () => {console.log(projects)});
    }
  }

  slideBack(){
    if(this.state.currentProjectIndex > 2)
    this.setState(({ currentProjectIndex }) => ({ currentProjectIndex: currentProjectIndex - 1 }), 
    () => {console.log(projects)});
  }

  render(){
    return (
      <>
        <div style={styleSettings.projectContainer}>
          <IosArrowBack beat={true} onClick={this.slideBack} fontSize="50px" color="white" />        
            <Grid container spacing={3} style={styleSettings.gridContainer}>
                {this.projectItems()}                
            </Grid>
          <IosArrowForward beat={true} onClick={this.slideFoward} fontSize="50px" color="white" />
        </div>
      </>
    );
  }
}

const styleSettings = {
  projectContainer: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'center',
      marginBottom: '8%',
      alignItems: 'center',
      width: '80%'
  },
  projectHeader: {
    display: 'flex',
    alignSelf: 'flex-start',
    textAlign: 'left',
    marginLeft: "4%",
    fontSize: '5em',
    backgroundColor: 'yellow',
  },

  gridContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  }
}

export default Projects;